body,
button,
input,
select,
optgroup,
textarea {
	color: $color__text-main;
	font-family: $font__main;
	line-height: $font__line-height-body;
	@include font-size(.9);

	@include media-breakpoint( md ) {
		line-height: 1.2;
		@include font-size( 1.1 );
	}
}

@import "headings";

@import "copy";
